import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-detail-table',
  templateUrl: './detail-table.component.html',
  styleUrls: ['./detail-table.component.scss']
})
export class DetailTableComponent {

  reportConfig: any =  {
    sales_btax : this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_table
  }
  details: any = { quantity: 0, sales_atax: 0, sales_btax: 0, docs: [], expand: false };
  stateObj: any;
  table: any;
  heightScroll: any
  constructor(
    private router: Router,
    private vhQueryCafe: VhQueryCafe,
    private languageService: LanguageService,
    public vhAlgorithm: VhAlgorithm,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe
  ) {
    let data: any = this.router.getCurrentNavigation().extras.state
    if (data) {
      this.stateObj = data.stateObj;
      this.table = data.table;

      if (data.page2Detail) this.details = data.page2Detail
      else this.getSalesDetail(this.table);
    }
  }

  ngAfterViewChecked() {
    if (document.getElementById("sales-table-detail") && document.querySelector(".ant-table-thead") && document.querySelector(".sales-table-detail-header")) {
      this.heightScroll = (document.getElementById("sales-table-detail").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".sales-table-detail-header").clientHeight) - 90 + "px";
    }
    this.cdRef.detectChanges();
  }

  /**
   * get dữ liệu báo cáo
   * @example this.getSalesDetail();
   */
  getSalesDetail(table) {
    this.vhQueryCafe.getReportSales_Tables_byProducts_Vouchers_PaymentCards(this.stateObj.dataReport['startTime'], this.stateObj.dataReport['endTime'], this.table.id_table, this.stateObj.id_branch_report)
      .then((res) => {
        this.details = {
          ...res, expand: false, docs: res.docs.map((item, index) => {
            return { ...item, expand: false, index }
          })
        }
      })
  }
  //** hàm sort cho các cột */
  statusQuantity = false
  statusSales = false
  statusbSales = false
  statusName = false
  /**
   * sort với trường là dạng số
   * @param sort 
   * @param value tên trường cần sort
   * @example this.sort(1, 'total')
   */
  sort(sort, value) {
    if (sort) {
      this.details.docs = this.vhAlgorithm.sortNumberbyASC([...this.details.docs], value)
    }
    else {
      this.details.docs = this.vhAlgorithm.sortNumberbyDESC([...this.details.docs], value)
    }
  }
  /**
   * sort với trường là dạng chuỗi
   * @param sort 
   * @param value tên trường cần sort
   * @example this.sort(1, 'name')
   */
  sortName(sort, value) {
    if (sort) {
      this.details.docs = this.vhAlgorithm.sortVietnamesebyASC([...this.details.docs], value)
    }
    else {
      this.details.docs = this.vhAlgorithm.sortVietnamesebyDESC([...this.details.docs], value)
    }
  }

  goToDetail(item) {
    let product = { ...item, id_table: this.table.id_table }
    setTimeout(() => this.router.navigate(['/cafe/dashboard/management/report/sales-table/detail-table/detail'], { state: { product, table: this.table, stateObj: this.stateObj, page2Details: this.details } }), 100)
  }
  /**
   * đóng/mở subs
   * @param item 
   */
  expandDish(item) {
    item.expand = !item.expand
    if (!item.expand) this.details['expand'] = false
  }
  /**
   * đóng/mở sub tất cả món
   * @example this.expandDishAll()
   */
  expandDishAll() {
    this.details.expand = !this.details.expand
    this.details.docs.forEach((item) => item.expand = this.details.expand)
  }

  format_date(date) {
    return this.datePipe.transform(date, "dd_MM_yyyy")
  }

  status: boolean = false;
  /**
   * xuất dữ liệu sang file excel và tải về
   */
  export() {
    this.status = true;
    let data = [];

    let name = `${this.languageService.translate("_table_sales_report")}_${this.vhAlgorithm.changeAlias(this.table.name).replace(/\s/g, "_")}_${this.languageService.translate("_from")}_${this.format_date(this.stateObj.dataReport.startTime)}_${this.languageService.translate("_to")}_${this.format_date(this.stateObj.dataReport.endTime)}_${this.stateObj['id_branch_report'] == "all" ? "" : this.vhAlgorithm.changeAlias(this.vhQueryCafe.getlocalBranch(this.stateObj['id_branch_report']).name).replace(/\s/g, "_")}`;

    let total: any = new Object();
    total[this.languageService.translate("No.")] = "";
    if (this.reportConfig.product_picture)
      total[this.languageService.translate("Image")] = '';
    total[this.languageService.translate("Product name")] = this.languageService.translate("Total");
    total[this.languageService.translate("Unit")] = "";
    total[this.languageService.translate("Quantity")] = this.details['quantity']
    if (this.reportConfig.sales_btax)
      total[this.languageService.translate("Sales before tax")] = this.details['sales_btax']
    total[this.languageService.translate("Sales after tax")] = this.details['sales_atax']
    total[this.languageService.translate("Barcode")] = "";
    data.push(total);

    for (let _rows of this.details['docs']) {
      let item: any = new Object();
      item[this.languageService.translate("No.")] = _rows.index;
      if (this.reportConfig.product_picture)
        item[this.languageService.translate("Image")] = _rows.img;
      item[this.languageService.translate("Product name")] = _rows.name
      item[this.languageService.translate("Unit")] = _rows.unit;
      item[this.languageService.translate("Quantity")] = _rows.quantity
      if (this.reportConfig.sales_btax)
        item[this.languageService.translate("Sales before tax")] = _rows.sales_btax
      item[this.languageService.translate("Sales after tax")] = _rows.sales_atax
      item[this.languageService.translate("Barcode")] = _rows.barcode;

      data.push(item);

      if (_rows['subs'])
      for (let subs of _rows['subs']) {
        let item: any = new Object();
        item[this.languageService.translate("No.")] = '';
        if (this.reportConfig.product_picture) {
          item[this.languageService.translate("Image")] = subs.name;
          item[this.languageService.translate("Product name")] = "";
        } else item[this.languageService.translate("Product name")] = subs.name;
        item[this.languageService.translate("Unit")] = "";
        item[this.languageService.translate("Quantity")] = subs.quantity
        if (this.reportConfig.sales_btax)
          item[this.languageService.translate("Sales before tax")] = subs.sales_btax
        item[this.languageService.translate("Sales after tax")] = subs.sales_atax
        item[this.languageService.translate("Barcode")] = ""

        data.push(item);
      }
    }
    this.vhAlgorithm.exportXLSX(data, name).then(() => this.status = false).catch(err => this.status = false)
  }

  expandAll: boolean = false;
  expands(item) {
    item.expand = !item.expand
  }
  goBack() {
    this.router.navigate(['/cafe/dashboard/management/report/sales-table'], { state: { stateObj: this.stateObj } })
  }

}
